body{
background-image: url(https://static.vecteezy.com/system/resources/previews/043/288/955/non_2x/ice-cream-on-an-empty-pastel-background-summer-background-with-ice-cream-and-copy-space-summer-time-composition-for-web-banner-cards-invitations-photo.jpg);
 background-repeat: no-repeat;
  background-size: cover;
}
*{
  margin: 0px;
  padding: 0px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.heading{
  width: 100%;
  height: 10vh;
  background-color: aliceblue;
  display: flex;

}
.details{
padding-top: 15px;
margin-left: 30%;
color:rgb(15, 62, 203);
}
.brand{
 
 font-style: italic;
 padding: 12px;
 color: blueviolet;
 display: block;
}
.order{
  margin-right: 45px;
 padding-bottom: 30px;
}
.Form{
  width: 40%;
  height: 120vh;
  background-color:rgb(236, 236, 244);
  margin-left: 30%;
  margin-top: 20px;
  border-radius: 8px;
  
}
.background-color{
  opacity: 0.5;
}

label{
margin-left: 20%;
  padding: 8px;
  text-align: justify;
  font-size: larger;
  color: rgb(14, 13, 13);
 
  font-style: italic;
}
::placeholder{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: small;
  
}
input{
  font-size: larger;
  margin-left: 5%;
  height: 30px;
  border-radius: 8px;
  width: 50%;
}

.inputs td,label{
  display: block;

}
.flavour, .gender{
  font-size: x-large;
  width: 50%;
  margin-left:  27%;
  display: block;
  border-radius: 8px;
}

.btn{
  margin: 5% 20%;
  width: 60%;
  height: 5vh;
  background-color: rgb(34, 210, 11);
  color: rgb(238, 233, 233);
  font-size: x-large;
  border-radius: 5px;
  border-color: #4cd918;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.address{
  height: 20vh;
}